<template>
  <div class="login">
    <div class="title">
      <div>
        <h1>独山子</h1>
        <p>大检修移动端</p>
      </div>
      <div>
        <img :src="login_img">
      </div>
    </div>
    <div class="login-form">
      <van-form @submit="onSubmit">
        <van-field
          v-model="username"
          name="username"
          placeholder="请输入用户名"
          clearable
          :rules="[{ required: true, message: '请填写用户名' }]"
        />
        <van-field
          v-model="password"
          type="password"
          name="password"
          placeholder="请输入密码"
          clearable
          :rules="[{ required: true, message: '请填写密码' }]"
        />
        <van-checkbox v-model="checked" @change="changeChecked">是否记住账号密码</van-checkbox>
        <div style="margin: 16px;">
          <van-button round block type="info" native-type="submit">登录</van-button>
        </div>
      </van-form>
    </div>
    <div v-show="versionShow" class="current-version">{{currentVersion}}</div>
  </div>
</template>

<script>
import { Toast, Notify } from 'vant'
import JsEncrypt from 'jsencrypt'
export default {
  name: 'Login',
  data () {
    return {
      username: '',
      password: '',
      currentVersion: 'V 1.0.0',
      login_img: require('../assets/login_img.svg'),
      checked: false,
      versionShow: true,
    }
  },
  watch: {
    '$store.state.btnShow'(newVal) {
      console.log('watch', newVal)
      this.versionShow = newVal;
    }
  },
  async mounted () {
    // debugger
    const userMassage = JSON.parse(window.localStorage.getItem('userMassage'))
    if (userMassage && userMassage.checked) {
      this.username = userMassage.name
      this.password = userMassage.password
      // window.localStorage.clear()
      this.checked = userMassage.checked
    }
  },
  beforeDestroy() {

  },
  methods: {
    async onSubmit (values) {
      if (values.username && values.password) {
        Toast.loading('验证中...')
        setTimeout(async () => {
          const name = this.getRSApass(this.username)
          const password = this.getRSApass(this.password)
          try {
            const res = await this.$postHttp(`${this.$baseUrl}/UAMS/authEncrypt`, {
              encryptedUsername: name,
              encryptedPassword: password
            })
            console.log(res)
            if (res.status === 200) {
              if (this.checked) {
                window.localStorage.setItem('userMassage', JSON.stringify({
                  name: this.username,
                  password: this.password,
                  checked: true,
                  isLogin: true
                }))
              } else {
                window.localStorage.setItem('userMassage', JSON.stringify({
                  name: this.username,
                  password: this.password,
                  checked: false,
                  isLogin: true
                }))
              }
              const userCookie = res.data.name + '=' + res.data.value
              window.localStorage.setItem('userCookie', JSON.stringify(userCookie))
              const Version = await this.$getHttp(`${this.$baseUrl}/OS-PM/mobile/v1.0/getVersionInfo`, {
                params: {
                  clientVersion: '1.0.0'
                }
              })
              const data = await this.$getHttp(`${this.$baseUrl}/UAMS/users/currentUser`)
              console.log('currentUser3', data)
              if (data && data.data.id) {
                // window.localStorage.setItem('userId', JSON.stringify(userName.id))
                window.localStorage.setItem('user', JSON.stringify({userName: data.data.name, userId: data.data.id}))
              }
              console.log(data);
              console.log('VersionResData', Version);
              if (Version.status === 200) {
                if (Version.data.updateFlag === '1') {
                  this.$router.push({ name: 'update', query: { update: Version.data } })  // 暂时先禁掉更新页
                  
                } else if (Version.data.updateFlag === '2') { //  强制升级
                  const dtask = plus.downloader.createDownload(`${this.$baseUrl}/OS-PM/${Version.data.pkgUrl}`, {force: true}, function ( d, status ) {
                  console.log('createDownload',d, status)
                      if (status == 200) { // 下载成功    
                        var path = (d.filename);    
                        plus.runtime.install(path,{ force: true},function(){
                          plus.nativeUI.closeWaiting();
                          console.log("安装wgt文件成功！");
                          plus.nativeUI.alert("应用资源更新完成！",function(){
                            plus.runtime.restart();
                          });
                        },function(e){
                          plus.nativeUI.closeWaiting();
                          console.log("安装wgt文件失败["+e.code+"]："+e.message);
                          plus.nativeUI.alert("安装wgt文件失败["+e.code+"]："+e.message);
                        });
                      } else {//下载失败    
                        alert( "Download failed: " + status );
                      }      
                    })
                  dtask.start();  

                  let prg = 0;  
                  let showLoading = plus.nativeUI.showWaiting("正在下载");  
                  dtask.addEventListener('statechanged', function(task, status) {  
                    // 给下载任务设置一个监听 并根据状态  做操作  
                    switch (task.state) {  
                      case 1:  
                        showLoading.setTitle("正在下载");  
                        break;  
                      case 3:  
                        prg = parseInt((parseFloat(task.downloadedSize) / parseFloat(task.totalSize)) * 100);  
                        showLoading.setTitle("  正在下载" + prg + "%  ");  
                        break;  
                      case 4:  
                          plus.nativeUI.closeWaiting();  
                        //下载完成  
                        break;  
                    } 
                  })

                } else {  //  0 不用升级
                  this.$router.push({ name: 'VersionSelection' })
                }
              }
            } else {
              console.log(22)
              Notify({ type: 'warning', message: '账号或密码错误' })
            }
            Toast.clear()
          } catch (error) {
            console.log('失败', error)
            Notify({ type: 'warning', message: '账号或密码错误' })
            Toast.clear()
          }
        }, 1000)
      }
    },
    changeChecked(val) {
      // debugger
    },
    getRSApass (password) {
      const jse = new JsEncrypt()
      // 公钥
      const publicString = 'MIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQChnc2YD0YzJPx6QTh+/n7XzjR1ugFzrsmPolJh' +
                      'pCfWMKrSGbT7iX/Kgcm1AI+T95K4Jzv3KS42QTecZ1ziJ4Rr9Luzw+9ZCSjMZgrmAUbY5IeBaA6GzaSk8UWHZ4n5PL+GUGq+2f+COL' +
                      '7+KCS2AxEpaqDZVrJrIfg/UektdgNyzwIDAQAB'
      jse.setPublicKey(publicString)
      // 加密内容
      const encrypted = jse.encrypt(password)
      return encrypted
    }
  }
}
</script>

<style lang='less' scoped>
  * {
    padding: 0;
    margin: 0;
  }
  .login {
    height: calc(100% - 9rem);
    box-sizing: border-box;
    padding: 1.5rem 1rem;
    // height: 41.7rem;
    .title {
      display: flex;
      justify-content: space-between;
      div:nth-child(1) {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
      }
      h1 {
        font-weight: 500;
        font-size: 1.375rem;
      }
      p {
        font-size: 0.875rem;
        color: #808187;
      }
    }
    .login-form {
      margin-top: 4rem;
      .van-cell {
        box-sizing: border-box;
        margin: 2rem 0rem !important;
        padding-top: 1rem;
        ::v-deep .van-field__body {
          border: 1px solid #B4B4B4;
          border-radius: 0.25rem;
          height: 3rem;
          input {
            text-indent: 1rem;
          }
        }
      }
      .van-cell:nth-child(1) {
        ::v-deep .van-field__body::before {
          clear: both;
          content: "用户名";
          display: block;
          left: 0.7rem;
          top: -0.7rem;
          padding: 0rem 0.1rem;
          position: absolute;
          background: white;
          color: #1E1E1E;
        }
      }
      .van-cell:nth-child(2) {
         ::v-deep .van-field__body::before {
          clear: both;
          content: "密码";
          display: block;
          left: 0.7rem;
          top: -0.7rem;
          padding: 0rem 0.1rem;
          position: absolute;
          background: white;
          color: #1E1E1E;
        }
      }
    }
    .current-version {
      position: fixed;
      bottom: 1.5rem;
      left: 1rem;
      font-size: 0.75rem;
      color: #B4B4B4;
    }
  }
</style>
